import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ViewTimesheetRoutes } from './view-timesheet.routing';
import { ViewTimesheetComponent } from './view-timesheet.component';
import { MaterialModule } from '../app.module';
import { AgmCoreModule } from '@agm/core';
import { AgmJsMarkerClustererModule } from '@agm/js-marker-clusterer';

@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(ViewTimesheetRoutes),
        FormsModule,
        ReactiveFormsModule,
        MaterialModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyALhBWvI6EuL2Vul0ykYis51D_ckKtxVCc',
            libraries: ["places"]
        }),
        AgmJsMarkerClustererModule  
    ],
    declarations: [
        ViewTimesheetComponent
    ]
})

export class ViewTimesheetModule { }


