import { Component, OnInit, ElementRef, NgZone, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { CommonService } from "../services/commonService";
import { TimesheetService } from "../services/timesheetService";
import { MatDatepickerModule, MatNativeDateModule, DateAdapter } from '@angular/material';
import { MapsAPILoader } from '@agm/core';
import { } from 'googlemaps';

declare const $: any;
@Component({
    selector: 'app-view-timesheet',
    templateUrl: './view-timesheet.component.html',
    styleUrls: ['./view-timesheet.component.scss'],
    providers: [CommonService, TimesheetService]
})
export class ViewTimesheetComponent implements OnInit {
    public latitude: number;
    public longitude: number;
    public zoom: number;
    public timesheetTrack = {};
    public timesheetLatLong;
    public userProfile: any;
    public workOrdAsgtID: any
    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private timesheetService: TimesheetService,
        private activatedRoute: ActivatedRoute,
    ) { }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {

            this.activatedRoute.queryParams.subscribe((params: Params) => {
                if (params['woasgtID'] == undefined) {
                    this.router.navigate(['timesheet']);
                }
                else {
                    //var woAsgtID = params['woasgtID'];
                    this.workOrdAsgtID = params['woasgtID'];
                    this.getWorkOrderDetails(this.workOrdAsgtID);
                    this.getTimesheetDaily(this.workOrdAsgtID);
                    //this.latitude = -33.865143
                    //this.longitude = 151.209900;
                    this.zoom = 10;
                }
            });
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }

    approvedTimeSheet(status: any) {
        alert();
        this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));

        var contact = {
            ApprovedBy: this.userProfile.ID,
            ApprovalStatus: status
        };

        this.timesheetService.UpdateTimesheetTrack(this.commonService.baseApiUrl, this.workOrdAsgtID, contact).subscribe(
            data => {
                var datatSheet = JSON.stringify(data);
                var tSheetParse = JSON.parse(datatSheet);
                var tSheetList = JSON.parse(tSheetParse["_body"]);
                if (tSheetList != null) {
                    $('.loader').hide();
                }
            });
    }

    goToTimesheet() {
        this.router.navigate(['timesheet']);
    }

    getWorkOrderDetails(asgtID: any) {
        $('.loader').show();
        this.timesheetService.GetTimesheetTrack(this.commonService.baseApiUrl, asgtID).subscribe(
            data => {
                var dataTsheet = JSON.stringify(data);
                var tSheetParse = JSON.parse(dataTsheet);
                var tSheetList = JSON.parse(tSheetParse["_body"]);
                if (tSheetList != null) {
                    this.timesheetTrack = tSheetList;
                    $('.loader').hide();
                }
                else {
                    $('.loader').hide();
                }
            }
        );
    }
    getTimesheetDaily(asgtID: any) {
        $('.loader').show();
        this.timesheetService.GetTimesheetDaily(this.commonService.baseApiUrl, asgtID).subscribe(
            data => {
                var dataTsheet = JSON.stringify(data);
                var tSheetParse = JSON.parse(dataTsheet);
                var tSheetList = JSON.parse(tSheetParse["_body"]);
                if (tSheetList != null && tSheetList.length > 0) {
                    var logLat = tSheetList.filter(map => map.StartLat && map.StartLong);
                    this.latitude = logLat[0].StartLat;
                    this.longitude = logLat[0].StartLong;
                    this.timesheetLatLong = tSheetList;
                    $('.loader').hide();
                }
                else {
                    $('.loader').hide();
                }
            }
        );
    }
}



